/* footer.css */

#footer {

    background: #17181C;
    color: #DC4280;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}
  
.left-side {
    max-width: 40%;
    color: #DC4280;
    font-family: 'Outfit', sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 23px;
}

.right-side {
    display: flex;
    gap: 20px;
    align-items: center; /* Add this line to center the icons vertically */
}

.icon {
    fill: #DC4280;
    width: 33px;
    height: 28px;
}

/* footer.css */

#scrollToTop {
    background: #17181C;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    margin-left:-120px;
}
