.rectangle2 {
    display: block;
    align-items: center;
    height: 240px;
    width: 100%;
    background-color: #24253A;
    margin-top: 30px;
    border-top: 8px solid #fff;
    border-bottom: 8px solid #fff;
}

.rectangle2 > *{
    display: flex;  /* Corrected property */
    align-items: center;
    margin-left: 470px;
}


.topic{
    color: #545583;
    text-align: center;
    font-family: Outfit;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: 66px; 
    margin-left: 20px;
}

.current{
    color: #FFF;
    text-align: center;
    font-family: Outfit;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: 66px; 
    margin-left: 20px;
}