.PetProjects h1{
  color: #FFF;
  text-align: center;
  font-family: Outfit;
  font-size: 50px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px; 
  margin-top: 30px;
}




.carousel-container {
  position: relative;
  height: 580px;
  width: 50%;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  border: 5px solid #545583;
  overflow: hidden;
  transition: background-color 0.3s ease;
}

.carousel-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(13, 21, 36, 0);
  transition: background-color 0.3s ease;
  z-index: 1;
}

.carousel-item img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: 0;
}

.carousel-caption {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 2;
  width: 80%; /* Set width directly */
  max-width: 100%; /* Ensure it doesn't exceed the container width */
}

.carousel-caption h3 {
  color: #FFF;
  text-align: center;
  font-family: 'Outfit', sans-serif;
  font-style: normal;
  font-size: 50px;
  font-weight: 700;
  line-height: 19px; /* 38% */
}


.carousel-caption h2{
  color: #FFF;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 19px; /* 105.556% */
  margin-top:30px;
}

.carousel-caption p{
  color: #DC4280;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 19px; /* 158.333% */
  margin-top: 20px; /* Adjust margin as needed */
}
.carousel-container:hover::before {
  background-color: rgba(13, 21, 36, 0.7);
}

.carousel-container:hover .carousel-caption {
  opacity: 1;
}

.carousel-container .carousel-indicators .active {
  background-color: #DC4280; 
}

