.rectangle3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 580px;
    width: 100%;
    background-color: #24253A;
    margin-top: 50px;
    border-top: 8px solid #fff;
    border-bottom: 8px solid #fff;
  }
  .title3{
    color: #FFF;
    text-align: center;
    font-family: Outfit;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    line-height: 60px; 
    margin-bottom: 20px;
  }
  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .input-box {
    margin-bottom: 20px;
  }
  
  input,
  textarea {
    width: 554px;
    height: 48px;
    flex-shrink: 0;
    color: #fff; 
    text-align: left;
    font-family: 'Outfit';
    font-size: 25px;
    font-style: normal;
    font-weight: 700;
    line-height: 66px; 
    background: #545583;
    border: none;
    padding: 0 10px; 
    margin-bottom: 10px; 
    outline: none;
  }
  
  textarea {
    height: 288px; 
  }
  
  button {
    width: 165px;
    height: 48px;
    flex-shrink: 0;
    border: 3px solid #dc4280;
    background: #545583;
    color: #dc4280;
    text-align: center;
    font-family: 'Outfit';
    font-size: 25px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    cursor: pointer;
    outline: none;
    margin-top: 10px; 
  }
  
.submit{
  &:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  }
}
  
