html {
  zoom: 85%;
}

body {
  background-color: #17181C;
}

/* App.css */

.main-rectangle {
  width: 100%;
  height: 102px;
  background-color: #24253a;
  position: relative;
  overflow: hidden;
  margin-top: 20px; /* Adjust as needed */
}

.main-rectangle::before,
.main-rectangle::after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
}

.main-rectangle::before {
  top: 0;
}

.main-rectangle::after {
  bottom: 0;
}
